.menu_top {
  z-index: 998;
}

.menu_top ul li {
  position: relative;
  text-align: center;
  display: block;
}

.menu_top ul li a {
  position: relative;
  padding: 0 10px;
  font-weight: 700;
  text-transform: uppercase;
  display: inline-block;
}

.menu_top ul li a:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 8px;
  background: #d1c0a8;
  transform: translateY(-50%) scaleX(0);
  transform-origin: right;
  transition: 0.5s transform;
}

.menu_top ul li a:hover:before {
  transform: translateY(-50%) scaleX(1);
  transform-origin: left;
  transition: 0.5s transform;
}

#toggleIcon {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  /* background: #d1c0a8; */
  z-index: 999;
  cursor: pointer;
}

